.cards-pack {
  margin: 40px 0 0px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  &__title {
    font-family: 'Poppins';
    font-size: 22px;
    line-height: 33px;
    margin: 0 0 15px 0;
  }
  &__list {
    cursor: pointer;
  }

  &__wrapper {
    position: relative;
    width: 80%;
    height: 700px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 0 0px 0;
    &_table {
      width: 75%;
      height: 99.5%;
      background-color: #fefeff;
      padding: 24px 48px;
      border-top-right-radius: 10px;
      border-bottom-right-radius: 10px;
      overflow-y: scroll;
      &_table {
        background-color: black;
        position: relative;
      }
    }

    &_schow {
      display: flex;
      flex-direction: column;
      align-items: center;
      height: 106.3%;
      width: 20%;
      background-color: #ececf9;
      border-top-left-radius: 10px;
      border-bottom-left-radius: 10px;
    }
  }
  &__search {
    margin: 0 30px 24px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;

    &__icon {
      position: absolute;
      top: 9px;
      left: 18px;
    }

    &__input {
      width: 400px;
      height: 36px;
      padding: 0 80px 0 40px;
      outline: none;
      background-color: #ececf9;
      border: 1px solid #635d80;
      border-radius: 2px;
    }
    &__button {
      position: absolute;
      right: 10px;
      top: 6px;
      height: 25px;
      width: 70px;
      border: none;
      background-color: #9a91c8;
      cursor: pointer;
      align-items: center;
      transition: 1s;
      &:hover {
        background-color: #8279ac;
      }
    }
  }
  &__input-wrapper {
    position: relative;
  }

  &__pagination {
    display: flex;
    align-items: center;
  }
}

.demo-simple-select-label {
  color: black !important;
}

.cards-pack__wrapper_table::-webkit-scrollbar {
  width: 0px;
  height: 5px;
}

.cards-pack__wrapper_table::-webkit-scrollbar-track {
  background: rgb(195, 177, 223);
  border-radius: 5px;
}

.cards-pack__wrapper_table::-webkit-scrollbar-thumb {
  background: rgb(111, 96, 245);
  border-radius: 5px;
}
.cards-pack__wrapper_table::-webkit-scrollbar-thumb:hover {
  background: rgb(100, 100, 100);
  border-radius: 5px;
}
.cards-pack__wrapper_table::-webkit-scrollbar-thumb:active {
  background: rgb(68, 68, 68);
  border-radius: 10px;
}

.MuiTouchRipple-root {
  color: #21268f !important;
}

.MuiButtonBase-root {
  color: #21268f !important;
  font-weight: 400 !important;
  font-size: 18px !important;
}

.Mui-selected {
  color: #fff !important;
}

.MuiPaginationItem-page.Mui-selected {
  background-color: #21268f !important;
  color: #fff;
  border-radius: 5px;
}

.cards-pack__wrapper_table {
  position: relative;
}
